import { computed, unref } from "vue";
import { isCodeActive } from "../settings";
import _ from "lodash";
export function fieldValComputedArrayToListWithOnes(varRef) {
    return computed({
        get() {
            const chosen = varRef.value || {};
            return Object.keys(chosen).map(key => chosen[key] === "1" ? key : false).filter(val => val);
        },
        set(val) {
            const result = {};
            for (const key of val) {
                result[key] = "1";
            }
            varRef.value = result;
        }
    });
}
export function computedListOfAvailablePostTypes(varRef) {
    return computed(() => {
        const result = {};
        _.forEach(varRef.value, (value, key) => {
            result[key] = {
                key: key,
                label: value,
                disabled: unref(isCodeActive()) ? false : !['post'].includes(key)
            };
        });
        return result;
    });
}
